//main splash
if( document.getElementsByClassName( 'kne-main-splash' ).length > 0 ){

  function findElInChildren( parent, searchParam, searchType ){
    let target = null;
    let elChildren = parent.childNodes;
    [...elChildren].forEach( function( child ){

      if( searchType == 'tag'){
        if( child.tagName.toLowerCase() == searchParam ){
          target = child;
        }
      } else {
        //default to search by class
        if( child.classList.contains( searchParam )){
          target = child;
        }
      }
    })
    return target;
  }

  let mainSplashes = document.getElementsByClassName('kne-main-splash');
  [...mainSplashes].forEach( function( mainSplash ){

    let buttonContainer = findElInChildren( mainSplash, 'button-container', 'class');
    let playButton = findElInChildren( buttonContainer, 'play', 'class');
    let videoContainer = findElInChildren( mainSplash, 'video-container', 'class');
    let videoEl = findElInChildren( videoContainer, 'video', 'tag');
    let stopButton = findElInChildren( videoContainer, 'stop', 'class');

    //add video ended event listener
    videoEl.addEventListener( 'ended', e => { mainSplash.classList.remove('play') })
    //play button listener
    playButton.addEventListener( 'click', (e) => { mainSplash.classList.add('play'); videoEl.play(); });
    //stop button listener
    stopButton.addEventListener( 'click', (e) => { mainSplash.classList.remove('play'); videoEl.pause(); });
  })
}

//top videos
if( document.getElementsByClassName( 'top-video-container' ).length > 0 ){
  let topVideos = document.getElementsByClassName( 'top-video-container' );

  [...topVideos].forEach( function( topVideoContainer ){

    let elChildren = topVideoContainer.childNodes;
    let playButton = null;

    [...elChildren].forEach( function( child ){
      //play button
      if( child.classList.contains( 'play' )){
        child.addEventListener( 'click', () => { topVideoContainer.classList.add('active') })
      }

      if( child.classList.contains( 'video-container' )){
        [...child.childNodes].forEach( function( videoContChild ){
          if( videoContChild.classList.contains( 'closeModal' )){
            videoContChild.addEventListener( 'click', () => { topVideoContainer.classList.remove('active') })
          }
        })
      }
    })
  })
}
