if( typeof edge !== 'undefined' ){
  if( edge ){
    document.body.classList.add('edge-browser');
  }
}
//add ie or edge classes
if( typeof ie !== 'undefined' ){
  if( ie ){
    document.body.classList.add('ie-browser');
    //remove add to cart forms
    var allForms = document.getElementsByTagName('form');
    // console.log(allForms);
    for( var x = 0; x < allForms.length; x++ ){
      var form = allForms[x];
      if( form.classList.contains('cart') ){
        form.parentNode.removeChild(form);
      }
    }
    //get page url
    let pathname = window.location.pathname;
    //remove all children
    if( pathname.indexOf('booking') > -1 ){
      while (document.body.firstChild) {
        document.body.removeChild(document.body.firstChild);
      }
    }
  }
}


//leaderboards
if(document.getElementsByClassName('leaderboard_container').length > 0){

  //set header year
  let leaderboardHeader = document.getElementsByClassName('leaderboard_title_yyyy')[0];
  let year = moment().format('YYYY');
  leaderboardHeader.innerHTML = 'Leaderboard '+year;

  let leaderboardContainer = document.getElementsByClassName('leaderboard_container')[0];
  let leaderboardSuppInfoContainer = document.getElementsByClassName('leaderboard_supp_info')[0];
  //add loading spinner to leaderboard
  addSpinner( leaderboardContainer );

  let leaderboard = new Leaderboard( 10 );

  leaderboard.loadItems()
    .then( leaders => {
      let preload = true;
      leaderboard.drawItems( leaderboardContainer, preload );
      leaderboard.drawFastest( leaderboardSuppInfoContainer );
    });

}

