import Api from '../utilities/api.js';
import moment from 'moment';
import {addSpinner, removeSpinner} from '../utilities/spinner.js';

function Leaderboard ( limit ) {
  this.limit = limit;

  this.leaders = [];
}

Leaderboard.prototype.loadItems = function () {
  return new Promise( function( resolve, reject ){
    this.getItems()
      .then( leaders => {
        this.leaders = leaders;
        resolve( leaders );
      })
      .catch( err => {
        reject(err)
      })

  }.bind(this))
}

Leaderboard.prototype.getItems = function () {
  return new Promise( function( resolve, reject ){

    let api = new Api();
    let base_url = api.base_url;
    let key = api.api;
    let limit = this.limit;
    let year = moment().format('YYYY');

    //get dates
    let start = year + '-01-01T00:00:00';
    let end = year + '-12-31T23:59:59';

    let getLeadersQueryString = api.base_url + 'races/fastest?key=' + api.api + '&start=' +  start +  '&end=' + end + '&limit=' + limit + '&speed_level=1';

    fetch( getLeadersQueryString )
      .then( response => api.checkResponse(response) )
      .then( response => api.returnJson(response) )
      .then( leaders => {
        resolve(leaders)
      })
      .catch( err => {
        reject(err)
      })

  }.bind(this))
};

Leaderboard.prototype.drawItems = function ( targetEl, preload ) {
  removeSpinner( targetEl );
  let tableEl = document.createElement('table');
  let theadEl = document.createElement('thead');
  let tbodyEl = document.createElement('tbody');
  //thead
  let headerRow = document.createElement('tr');
  let nameTh = document.createElement('th');
  let name = document.createTextNode('Name');
  let timeTh = document.createElement('th');
  let time = document.createTextNode('Lap Time');
  nameTh.appendChild(name);
  timeTh.appendChild(time);
  headerRow.appendChild(nameTh);
  headerRow.appendChild(timeTh);
  theadEl.appendChild(headerRow);
  tableEl.appendChild(theadEl);
  //tbody
  this.leaders.fastest.forEach( function(leader){
    let rowEl = document.createElement('tr');
    //if preload add class ot hide - show will be on scroll appear
    if(preload){
      rowEl.classList.add('preload');
    }
    let nameTd = document.createElement('td');
    let name = document.createTextNode(leader.nickname);
    nameTd.appendChild(name);
    let timeTd = document.createElement('td');
    let time = document.createTextNode(leader.lap_time + 's');
    timeTd.appendChild(time);
    rowEl.appendChild(nameTd);
    rowEl.appendChild(timeTd);
    tbodyEl.appendChild(rowEl);
  })
  tableEl.appendChild(tbodyEl);

  targetEl.appendChild(tableEl);
};

Leaderboard.prototype.drawFastest = function ( targetEl ) {
  let contEl = document.createElement('div');
  contEl.classList.add('fastest-info');
  //lap
  let lapHeader = document.createElement('h4');
  let lapHeaderText = document.createTextNode('Fastest Lap');
  lapHeader.appendChild(lapHeaderText);
  let lapInfo = document.createElement('p');
  let lapText = document.createTextNode( this.leaders.fastest[0].lap_time );
  lapInfo.appendChild(lapText);
  //driver
  let driverHeader = document.createElement('h4');
  let driverHeaderText = document.createTextNode('Fastest Driver');
  driverHeader.appendChild(driverHeaderText);
  let driverInfo = document.createElement('p');
  let driverText = document.createTextNode( this.leaders.fastest[0].nickname );
  driverInfo.appendChild(driverText);

  contEl.appendChild(lapHeader);
  contEl.appendChild(lapInfo);
  contEl.appendChild(driverHeader);
  contEl.appendChild(driverInfo);

  targetEl.prepend(contEl)

};

export default Leaderboard;
