// SETTING ACTIVE CLASSES
function addActiveClass(targetUrl) {
  
  let activityButtons = document.getElementsByClassName('activity_button');
  
  for (var i = 0; i < activityButtons.length; i++) {
  
    let buttonEl = activityButtons[i];
    let buttonHref = buttonEl.getAttribute("href");

    buttonEl.classList.remove("active");    
    if (targetUrl == buttonHref) {
      buttonEl.classList.add("active");
    }
  
  }
}

let hash = window.location.hash;
let pathName = window.location.pathname;
hash.replace('#', '');
let targetUrl = pathName + hash;
addActiveClass(targetUrl);

let activityButtons = document.getElementsByClassName('activity_button');

[...activityButtons].forEach( activityButton => {
  activityButton.addEventListener("click", e => { addActiveClass(e.currentTarget.pathname + e.currentTarget.hash)} );
})