
window.onscroll = function(){
  removePreloads();
}

//remove preload classes from table - for leaderboard
function removePreloads(){

  function removePreload( nodes, index, length ){
    if( index < length){
      nodes[index].classList.remove('preload');
      setTimeout( function () {
        removePreload( nodes, index + 1, length);
      }, 200);
    }
  }

  if( document.getElementsByClassName('preload').length > 0 ){
    let elements = document.getElementsByClassName('preload');
    let elCopy = Array.from(elements);
    if( elements.length > 0 ){
      let parent = document.getElementsByClassName('preload')[0].parentNode;
      let parentHeight = parent.clientHeight;
      let parentBottom = parent.getBoundingClientRect().bottom;
      if(parentBottom - (parentHeight/2) < window.innerHeight){
        //loop through each preload element with timeout
        removePreload( elCopy, 0, elements.length);
      } //triggers when half of el visible
    }
  }
}
