import {addSpinner, removeSpinner} from '../utilities/spinner.js';

function BookingEnquiryForm( form, formId ) {

  this.form = form;
  this.formId = formId;

  this.inputs = this.form.elements;
  this.name = this.inputs['name'];
  this.email = this.inputs['email'];
  this.phone = this.inputs['phone'];
  this.date = this.inputs['date'];
  this.event = this.inputs['event'];
  this.message = this.inputs['message'];
  this.qtyTotal = this.inputs['qty-total'];
  this.qtyCadets = this.inputs['qty-cadets'];
  this.submitButton = this.inputs['submit'];

  this.messageContainer = null;
  this.setMessageContainer();

  this.form.addEventListener( 'submit', e => { this.bookingEnquirySubmit( e ) } );

}


BookingEnquiryForm.prototype.bookingEnquirySubmit = function ( e ) {
  e.preventDefault();

  if(typeof _gaq !== 'undefined'){
    _gaq.push(['_trackEvent', 'Button Click Tracking', 'Booking Enquiry', 'Submit']);
  }

  this.submitButton.disabled = true;

  addSpinner( this.messageContainer );
  this.form.classList.add('submitting');

  let postObject = {
    formid: this.formId,
    name: this.name.value,
    email: this.email.value,
    phone: this.phone.value,
    date: this.date.value,
    event: this.event.value,
    message: this.message.value,
    qtytotal: this.qtyTotal.value,
    qtycadets: this.qtyCadets.value
  }

  console.log(postObject);

  fetch(
    '/booking-enquiry-submit',
    {
      method: 'POST',
      mode: 'same-origin',
      credentials: 'same-origin',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        'data': postObject
      })
    }).then( response => {
      if( !response.ok ){
        return Promise.reject(new Error(response.statusText))
      } else {
        return Promise.resolve(response)
      }
    }).then( response => {
      removeSpinner( this.messageContainer );
      let thankYouEl = document.createElement('div');
      thankYouEl.classList.add('submit-success-message');
      let thankYouText = document.createTextNode('Thank you for your submission!')
      thankYouEl.appendChild( thankYouText );
      this.messageContainer.appendChild( thankYouEl );
      this.submitButton.disabled = false;
      return true;
    })
    .catch( err => {
      console.log( err );
    })
};

BookingEnquiryForm.prototype.setMessageContainer = function () {
  let children = this.form.children;

  [...children].forEach( function( child ){
    if( child.classList.contains('message-container') ){
      this.messageContainer = child;
    }
  }.bind(this))
};


export default BookingEnquiryForm;
