import Reservation from '../booking/reservation.js';
import EventReservation from '../booking/eventReservation.js';
import Event from '../booking/event.js';
import Check from '../booking/check.js';
import Api from '../utilities/api.js';
import Storage from '../utilities/storage.js';
import {showOverlay, removeOverlay} from '../utilities/overlay.js';
import { addSpinner, removeSpinner } from '../utilities/spinner.js'; //ui js
import moment from 'moment';

function Cart( cart ){
  this.reservations = {};
  this.cart = cart;
  this.countdown = {
    timer: null
  };
  this.removeButtons = null;

  this.setRemoveButtons();
  this.constructReservations();
}

Cart.prototype.constructReservations = function () {
  const storage = new Storage();
  let reservationsFromStore = storage.retrieve('reservations');
  //construct reservations
  if( reservationsFromStore ){
    let resKeys = Object.keys(reservationsFromStore.items);
    for (const resKey of resKeys) {
      let res = reservationsFromStore.items[resKey];
      this.reservations[resKey] = this.constructReservation( res );
    }
  }
};

Cart.prototype.constructReservation = function ( res ) {
  let check = new Check(res.check.checkId, res.check.customerId, res.check.type, res.check.status, res.check.name, res.check.total, res.check.notes, res.check.openedDate)

  let event = null;
  if(res.event){
    event = new Event(res.event.eventDesc, res.event.eventDuration, res.event.eventId, res.event.eventScheduledTime,res.event.eventTypeId, res.event.eventTypeName, res.event.reservationId, res.event.trackNo, res.event.eventHeatTypes )
  }

  let eventReservation = new EventReservation( res.eventReservation.eventReservationId, res.eventReservation.description, res.eventReservation.endTime, res.eventReservation.eventTypeId, res.eventReservation.minNoOfAdultsPerBooking, res.eventReservation.minNoOfCadetsPerBooking, res.eventReservation.noOfCadetRacers, res.eventReservation.noOfRacers, res.eventReservation.noOfTotalRacers, res.eventReservation.notes, res.eventReservation.startTime, res.eventReservation.status, res.eventReservation.subject, res.eventReservation.externalSystemId, res.typeId, res.eventReservation.hidden );

  //if induction - turn into event reservation
  let inductionEventRes = null;
  if( res.induction ){
    inductionEventRes = new EventReservation( res.induction.eventReservationId, res.induction.description, res.induction.endTime, res.induction.eventTypeId, res.induction.minNoOfAdultsPerBooking, res.induction.minNoOfCadetsPerBooking, res.induction.noOfCadetRacers, res.induction.noOfRacers, res.induction.noOfTotalRacers, res.induction.notes, res.induction.startTime, res.induction.status, res.induction.subject, res.induction.externalSystemId, res.induction.typeId, null );
  }

  let reservation = new Reservation( res.reservationId, res.wcProductId, res.created, res.eventTypeId, check, res.checkDetails, res.customer, event, eventReservation, res.eventReservationLink, res.status, res.wcEventTitle, inductionEventRes);
  // this.reservations[res.reservationId] = reservation;

  return reservation;
};

Cart.prototype.setRemoveButtons = function () {
  let removeButtonsHtmlCollection = document.getElementsByClassName('kne-remove-cart-item');
  this.removeButtons = Array.from( removeButtonsHtmlCollection );
  this.removeButtons.forEach( function( removeButton ){
    removeButton.addEventListener('click', (e) => {this.triggerRemoveFromButton(e)} )
  }.bind(this))
};

Cart.prototype.triggerRemoveFromButton = function( e ){
  let removeButton = e.target;
  let removeUrl = removeButton.getAttribute('data-remove-url');
  let reservationId = removeButton.getAttribute('data-reservation-id');
  let eventReservationId = removeButton.getAttribute('data-event-res-id');
  let checkId = removeButton.getAttribute('data-check-id');

  this.removeItemFromCart( reservationId )
    .then( result =>{
      window.location.href = removeUrl;
    })
    .catch( err => {
      console.error( err );
      window.location.href = removeUrl;
    })
}

Cart.prototype.removeItemFromCart = function ( reservationId ) {

  return new Promise( function( resolve, reject ){
    console.info('removing item...');

    let overlay = showOverlay( true, true, null ) //sub and loading and no content

    let reservation = this.reservations[reservationId];
    let api = new Api();
    //promise chain - if reservation found - else just remove!!
    if( reservation ){
      console.info(reservation);

      //fire off log of clear cart
      fetch(`${appServerUrl}/api/v1/log-clear-cart/${reservationId}-${reservation.eventReservation.eventReservationId}-${reservation.check.checkId}-${reservation.customer.email}`)
        .then( result => {
          console.log(result);
        });


      reservation.check.voidCheck( api )
        .then( response =>
          reservation.eventReservation.removeCheck( reservation.check.checkId, api )
        )
        .then( checkCnt => {
          if( checkCnt > 0 || reservation.eventReservation.userId !== 2 ){
            return true;
          } else {
            // remove event if no more checks on event
            return new Promise( function( resolve, reject ){
              reservation.event.removeEventOnApi( api )
                .then( result =>
                  reservation.eventReservation.setStatus( 4, api )
                )
                .then( result => {
                  resolve( true );
                })
                .catch( err => {
                  reject( err );
                })
            })
          }
        })
        .then( response => {
          reservation.setStatusCancelled();
          resolve(true);
        })
        .catch(function(err) {
          reject(err)
        });
      } else {
        console.warning('Warning: No reservation found in storage!!!');
      }
  }.bind(this))
};

Cart.prototype.getLastAddedTime = function () {
  let lastAddedToCart = null;
  let endTime = null;
  if( this.cart ){
    let cartKeys = Object.keys( this.cart );

    cartKeys.forEach( function( key ){
      let cartItem = this.cart[key];
      if( typeof cartItem['added-to-cart'] !== 'undefined' ){
        //convert to moment
        let addedToCart = moment( cartItem['added-to-cart'], 'YYYY-MM-DD HH:mm:ss');
        //check if greater than last
        if( addedToCart > lastAddedToCart ){
          lastAddedToCart = addedToCart;
        }
      }
    }.bind(this))

    if( lastAddedToCart ){
      endTime = lastAddedToCart.add(10, 'm');
    }
  }

  return endTime;
};

Cart.prototype.countdownNext = function( targetEl, endTime, reservationIds ){
  if( endTime ){
    let now = moment();
    let difference = endTime.diff(now, 's');
    let diffMins = Math.floor(difference / 60);
    let diffSecs = ( difference % 60 < 10 ) ? '0' + (difference % 60) : difference % 60 ;

    //check if still past end time
    if( difference > 0 ){
      targetEl.innerHTML = 'You have ' + diffMins + ' mins and ' + diffSecs + ' seconds to &nbsp; <a href="/cart">checkout</a>';
    } else {
      clearInterval( this.timer );
      targetEl.innerHTML = 'Your cart items have been cleared - please &nbsp; <a href="/booking-start">book again</a>';
      //for each cart item if reservation id - clear
      reservationIds.forEach( function( reservationId ){
        this.removeItemFromCart( reservationId )
          .then( result => {
            window.history.pushState( {}, '', '?clear-cart=1');
            location.reload();
          })
          .catch( err => {
            console.error( err );
            window.history.pushState( {}, '', '?clear-cart=1');
            location.reload();
          })
      }.bind(this));
    }
  } else {
    clearInterval( this.timer );
  }
}

Cart.prototype.startCountdown = function () {
  //get reservation ids to remove if needed
  let reservationIds = [];
  let cartKeys = Object.keys( this.cart );
  cartKeys.forEach( function( key ){
    if( this.cart[key]['reservation-id'] ){
      reservationIds.push( this.cart[key]['reservation-id'] )
    }
  }.bind(this));

  if(reservationIds.length > 0){
    //countdown element
    let countdownContEl = document.createElement('div');
    countdownContEl.classList.add('countdown-container');
    document.body.appendChild(countdownContEl);
    let headerEl = document.getElementsByTagName('header')[0];
    headerEl.classList.add('countdown-active');
    let endTime = this.getLastAddedTime();

    this.timer = setInterval( () => {this.countdownNext( countdownContEl, endTime, reservationIds )}, 100 );
  }
};

export default Cart;
